
html {
  background: #F5F3F0;
  font-family: 'Raleway';
}
h1 {
  font-weight: 400;
  text-align: center;
}

.owl-carousel .owl-item {
  transition: opacity .8s ease;
}
.owl-carousel .owl-item:not(.active) {
  opacity: .3;
}

#sweetCalendar {
  margin: 1em;
}
#sweetCalendar .body {
    background: #FFF;
    border-radius: 10px;
}
#sweetCalendar .left-arrow, #sweetCalendar .right-arrow {
    display: none;
}
#sweetCalendar .header .month {
    font-weight: 400;
    font-size: 1.2em;
    color: #404040;
    text-align: center;
    margin-bottom: 1em;
}
.legend {
  margin-left: 3em;
}
.legend li {
    margin: 0;
    list-style: none;
    font-weight: 600;
    font-size: .8em;
    color: #404040;
    line-height: 1.8em;
    position: relative;
}
.legend .color {
    display: inline-block;
    position: absolute;
    left: -25px;
    width: 15px;
    height: 15px;
    background: Red;
    border-radius: 50%;
    line-height: 1.8em;
    margin-right: 10px;
    margin-top: 4px;
}

